.slider {
	height: 1em;
	position: relative;

	display: -ms-flexbox;

	display: flex;
	background-image: -webkit-gradient(linear, left top, right top, from(#892AFF), to(#23B2EE));
	background-image: -webkit-linear-gradient(left, #892AFF 0%, #23B2EE 100%);
	background-image: -o-linear-gradient(left, #892AFF 0%, #23B2EE 100%);
	background-image: linear-gradient(to right, #892AFF 0%, #23B2EE 100%);
	padding: 0 .5em;
	margin-bottom: 2em;
}

.slider.disabled{
	padding: 0;
	cursor: inherit;
}

.slider input {
	position: absolute;
	cursor: pointer;

	width: 100%;
	height: 200%;
	margin: 0;
	-ms-flex-item-align: center;
	    align-self: center;

	opacity: 0;
	left: 0;
}

.disabled input {
	cursor: inherit;
}

.slider__range-track {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row-reverse;
	    flex-flow: row-reverse;
	position: relative;
}

.slider__range-inverse {
	-ms-flex: 1 1;
	    flex: 1 1;
	position: relative;
	right: -.5em;

	background: #ccc;
}

.disabled .slider__range-inverse{
	right: inherit;
}

.slider__handle {
	width: 0;
	height: 1.4em;
	-ms-flex-item-align: center;
	    align-self: center;
	position: relative;
}

.slider__handle::after {
	content: '';
	display: block;
	-webkit-transform: translateX(-.7em);
	    -ms-transform: translateX(-.7em);
	        transform: translateX(-.7em);

	width: 1.4em;
	height: 1.4em;

	border-radius: 50%;
	background: white;
	-webkit-box-shadow: 0 0 6px rgba(0, 0, 0, .6);
	        box-shadow: 0 0 6px rgba(0, 0, 0, .6);
	cursor: pointer;
}

.disabled .slider__handle {
	display: none;
}

.slider__labels {
	display: -ms-flexbox;
	display: flex;
	position: absolute;
	width: 100%;
	height: 100%;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.slider__label-wrapper {
	position: relative;
	height: 100%;
	padding: 0 1px;
}

.slider__label-wrapper:not(:first-child):not(:last-child) {
	background: white;
}

.slider__label-wrapper label {
	position: absolute;
	bottom: -1.5em;
	right: 0;
}

.slider__label-wrapper:first-of-type label {
	right: inherit;
}

.slider__label-wrapper label {
	display: none;
}

.slider__label-wrapper label.active {
	display: inherit;
}



